.l-section {
  margin: $out auto;
}

.l-inner {
  width: 100%;
  max-width: $innerWidth;
  margin: 0 auto;
}

.logo {
  svg {
    height: 2.5em;
    color: $secondary;
    // opacity: 0.85;
  }
}

/**
 * Grid
 */
.c-grid {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;

  /** Column sizes */
  .c-col {
    flex-basis: 100%;
    @include sm {
      flex-basis: calc(100% / 2 - #{$gut} * 2);
    }
    + .c-col {
      // margin: $out 0;
      margin-top: $out;
    }
  }
  .-wide {
    width: 100%;
    flex-basis: 100%;
